.ExpertiseSection {
  padding: 5%;
  display: flex;
  flex-direction: column;
  width: 75%;
  margin: auto;
}

.ExpertiseSection h1 {
  text-align: center;
}

.cardGroup {
  display: flex;
}

.cardGroup h1 {
  text-align: left;
}

@media screen and (max-width: 1050px) {
  .ExpertiseSection {
    width: 90%;
  }
}

@media screen and (max-width: 850px) {
  .cardGroup {
    flex-direction: column;
  }
}
