@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@400;600;900&display=swap);
.titleHeading {
  font-size: 8rem;
  text-align: center;
  text-transform: uppercase;

  margin: 0;
}

.titleParagraph {
  font-size: 2rem;
  text-align: center;
  text-transform: uppercase;

  margin: 0;

  color: #fff;
}

.titleHeading-Part1 {
  color: #bb85fe;
}

.titleHeading-Part2 {
  color: #01dbc4;
}

@media screen and (max-width: 750px) {
  .titleHeading {
    font-size: 4rem;
  }

  .titleParagraph {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 600px) {
  .titleHeading {
    font-size: 3.5rem;
  }

  .titleParagraph {
    font-size: 1.3rem;
  }
}

@media screen and (max-width: 550px) {
  .titleHeading {
    width: 90%;
    margin: auto;
  }
  .titleParagraph {
    width: 80%;
    margin: auto;
  }
}

@media screen and (max-width: 400px) {
  .titleHeading {
    font-size: 3.5rem;
  }

  .titleParagraph {
    font-size: 1rem;
  }
}

.TitleSideBars {
  width: 2%;
  height: 50vh;
  position: absolute;
}

.leftSideBar {
  top: 10%;
  left: 0;

  background-color: #01dbc4;
}

.rightSideBar {
  bottom: 10%;
  right: 0;

  background-color: #bb85fe;
}

.TitleSection {
  height: 100vh;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  position: relative;
}

.TitleSection a {
  color: #000;
}

.instagramSocial {
  background-color: #01dbc4;
  width: 100%;
  text-align: center;

  position: fixed;
  left: 0;
  z-index: 100;
  top: 0;
}

.instagramSocial p {
  padding: 10px 20px;

  margin: 0;
  line-height: 1;
}

.colorOne {
  color: #bb85fe;
}

.colorTwo {
  color: #01dbc4;
}

.SectionHeading {
  font-size: 4rem;
}


.AboutSection {
  padding: 5%;
  display: flex;
}

.AboutLeft,
.AboutRight {
  width: 50%;
}

.AboutSection h1 {
  text-transform: uppercase;
}

.AboutSection p {
  color: #fff;
  font-size: 1.3rem;
  line-height: 1.5;
}

.AboutRight {
  display: flex;
  align-items: center;
  justify-content: center;
}

.AboutRight img {
  width: 50%;
}

@media screen and (max-width: 800px) {
  .AboutSection {
    flex-direction: column;
  }

  .AboutLeft,
  .AboutRight {
    width: 100%;
  }

  .AboutRight {
    padding-top: 20px;
  }
}

.card {
  border: 2px solid #fff;
  padding: 20px;
  margin: 20px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  flex: 1 1;

  color: #fff;
}

.card h1 {
  text-transform: uppercase;
}

.card p {
  font-size: 1.2rem;
}

.ExpertiseSection {
  padding: 5%;
  display: flex;
  flex-direction: column;
  width: 75%;
  margin: auto;
}

.ExpertiseSection h1 {
  text-align: center;
}

.cardGroup {
  display: flex;
}

.cardGroup h1 {
  text-align: left;
}

@media screen and (max-width: 1050px) {
  .ExpertiseSection {
    width: 90%;
  }
}

@media screen and (max-width: 850px) {
  .cardGroup {
    flex-direction: column;
  }
}

